<!-- .:::: TEMPLATE ::::. -->
<template>

    <!-- Seo de cartes -->
    <div v-if="!adding_card && !updating_card">

        <!-- Informaes de rodap -->
        <h2 class="form-h2">Cartes de Crdito</h2>
        <p class="text-common-color">Quantia de elementos cadastrados: {{cards_data.length}}.</p><br>

        <!-- Cartes de crdito cadastrados -->
        <table class="form-table" v-if="cards_data.length > 0">

            <!-- Cabealho da tabela -->
            <tr class="form-table-header-row">
                <th class="form-table-title-item">Ttulo do Carto</th>
                <th class="form-table-center-item">Nmero do Carto</th>
                <th class="form-table-last-item">Modificar Carto</th>
            </tr>

            <!-- Itens da tabela -->
            <tr 
                v-for="(card, index) in cards_data" 
                :key="card" 
                :class="index % 2 === 0 ? 'form-table-item-row-0' : 'form-table-item-row-1'">
                <td>
                    <label>{{card.title}}</label>
                </td>
                <td>
                    <label>{{card.number.substring(0,5) + "..."}}</label>
                </td>
                <td>
                    <a @click="start_card_update(card)">Modificar</a>
                </td>
            </tr>

        </table>

        <!-- Boto de adio de carto -->
        <button @click="start_card_addition()" class="form-button standard-button">Adicionar carto</button>

    </div>

    <!-- Seo de adio e modificao de cartes -->
    <div v-if="adding_card || updating_card">

        <!-- Ttulo do carto -->
        <h2 class="form-h2">Ttulo do Carto</h2>
        <p class="text-common-color" style="margin-bottom: 1rem;">(somente para referncia)</p>
        <input 
            v-model="card_title" 
            type="text"
            class="form-info-container text"
            :class="{'form-normal-input-text': card_title_is_valid && !card_title_is_empty}">
        <p v-if="!card_title_is_valid" class="form-failed-input-text">O ttulo informado  invlido (deve conter somente caracteres alfanumricos).</p>
        <p v-if="card_title_is_empty" class="form-failed-input-text">Este campo  obrigatrio.</p>

        <div class="form-double-section">

            <!-- Nmero do carto -->
            <div class="form-vertical-section">
                <h2 class="form-h2">Nmero do Carto</h2>
                <input 
                    v-model="card_number" 
                    type="text" 
                    placeholder="1111 2222 3333 4444"
                    maxlength="19"
                    class="form-info-container text"
                    :class="{'form-normal-input-text': card_number_is_valid && !card_number_is_empty}">
                <p v-if="!card_number_is_valid" class="form-failed-input-text">O nmero informado  invlido.</p>
                <p v-if="card_number_is_empty" class="form-failed-input-text">Este campo  obrigatrio.</p>
            </div>

            <!-- Proprietrio do Carto -->
            <div class="form-vertical-section">
                <h2 class="form-h2">Proprietrio do Carto</h2>
                <input 
                    v-model="cardholder" 
                    placeholder=""
                    type="text" 
                    class="form-info-container text"
                    :class="{'form-normal-input-text': cardholder_is_valid && !cardholder_is_empty}">
                <p v-if="!cardholder_is_valid" class="form-failed-input-text">O nome informado  invlido.</p>
                <p v-if="cardholder_is_empty" class="form-failed-input-text">Este campo  obrigatrio.</p>
            </div>
        
        </div>

        <div class="form-double-section">

            <!-- Data de validade do carto -->
            <div class="form-vertical-section">
                <h2 class="form-h2">Data de Validade</h2>
                <input 
                    v-model="expiration_date" 
                    type="month" 
                    class="form-info-container text"
                    :class="{'form-normal-input-text': expiration_date_is_valid && !expiration_date_is_empty}">
                <p v-if="!expiration_date_is_valid" class="form-failed-input-text">A data informada  invlida.</p>
                <p v-if="expiration_date_is_empty" class="form-failed-input-text">Este campo  obrigatrio.</p>
            </div>

            <!-- Cdigo de Segurana -->
            <div class="form-vertical-section">
                <h2 class="form-h2">Cdigo de Segurana</h2>
                <input 
                    v-model="security_code" 
                    placeholder=""
                    type="text" 
                    maxlength="3"
                    class="form-info-container text"
                    :class="{'form-normal-input-text': security_code_is_valid && !security_code_is_empty}">
                <p v-if="!security_code_is_valid" class="form-failed-input-text">O cdigo informado  invlido.</p>
                <p v-if="security_code_is_empty" class="form-failed-input-text">Este campo  obrigatrio.</p>
            </div>

        </div>

        <!-- Confirmao de senha -->
        <h2 class="form-h2">Confirme sua Senha</h2>
        <input 
            v-model="password" 
            placeholder=""
            type="password" 
            class="form-info-container text"
            :class="{'form-normal-input-text': password_is_valid && !password_is_empty}">
        <p v-if="!password_is_valid" class="form-failed-input-text">A senha informada  invlida.</p>
        <p v-if="password_is_empty" class="form-failed-input-text">Este campo  obrigatrio.</p>

        <!-- Botes de ao -->
        <div class="form-update-buttons-section">

            <!-- Adio -->
            <button v-if="adding_card" @click="add_card()" class="form-button standard-button">Salvar Carto</button>
            <button v-if="adding_card" @click="cancel_card_addition()" class="form-button gray-button">Cancelar</button>

            <!-- Atualizao -->
            <button v-if="updating_card" @click="update_card()" class="form-button standard-button">Salvar Carto</button>
            <button v-if="updating_card" @click="delete_card()" class="form-button red-button">Remover Carto</button>
            <button v-if="updating_card" @click="cancel_card_update()" class="form-button gray-button">Cancelar</button>

        </div>

    </div>

</template>


<!-- .:::: SCRIPT ::::. -->
<script>

    // Para manipulao do banco de dados
    import store from '@/store';
    import { add_credit_card, delete_credit_card, select_user_credit_cards, update_credit_card } from '@/utils/database-management';

    // Para manipulao de formulrios
    import { validate_attribute_by_callback, validate_attribute_by_regex, validate_password_by_id } from '@/utils/form-validation';
    import { alphanumeric_parser } from '@/utils/utils';

    // Lgica local
    export default {

        // Nome do componente
        name: "ProfilePayment", 

        // Dados locais
        data() {
            return {

                // Cartes de Crdito
                total_database_cards: 0, 
                cards_data: [], 
                target_card_id: "", 

                // Para controle de adio e modificao de cartes de crdito
                adding_card: false, 
                updating_card: false, 

                // Controle de ttulo
                card_title: "", 
                card_title_is_valid: true, 
                card_title_is_empty: false, 

                // Controle de nmero de carto
                previous_card_number: "", 
                card_number: "", 
                card_number_is_valid: true, 
                card_number_is_empty: false, 

                // Controle de data de validade
                expiration_date: "", 
                expiration_date_is_valid: true, 
                expiration_date_is_empty: false, 

                // Controle de cdigo de segurana
                security_code: "", 
                security_code_is_valid: true, 
                security_code_is_empty: false, 

                // Controle de nome de proprietrio
                cardholder: "", 
                cardholder_is_valid: true, 
                cardholder_is_empty: false,
                
                // Controle de senha
                password: "", 
                password_is_valid: true, 
                password_is_empty: false, 
            };
        }, 

        // Aquisio dos dados do usurio
        created() {
            select_user_credit_cards(store.getters.user_id).then(res => {
                if(res != null) {
                    this.cards_data = res;
                }
            });
        }, 

        // Mtodos auxiliares
        methods: {
            
            // Inicializa a adio de um novo carto de crdito
            start_card_addition() {
                if(this.updating_card === true){
                    this.cancel_card_update();
                }
                this.adding_card = true;
                window.scrollTo(0,60);
            }, 

            // Redefine os dados de formulrio
            reset_card_form() {

                // Ttulo
                this.card_title = "";
                this.card_title_is_valid = true;
                this.card_title_is_empty = false;

                // Nmero de carto
                this.previous_card_number = "";
                this.card_number = "";
                this.card_number_is_valid = true;
                this.card_number_is_empty = false;

                // Data de validade
                this.expiration_date = "";
                this.expiration_date_is_valid = true;
                this.expiration_date_is_empty = false;

                // Cdigo de segurana
                this.security_code = "";
                this.security_code_is_valid = true;
                this.security_code_is_empty = false;

                // Nome de proprietrio
                this.cardholder = "";
                this.cardholder_is_valid = true;
                this.cardholder_is_empty = false;
                
                // Senha
                this.password = "";
                this.password_is_valid = true;
                this.password_is_empty = false;
            }, 

            // Cancela a adio de um novo carto de crdito
            cancel_card_addition() {
                this.adding_card = false;
                window.scrollTo(0,60);
                this.reset_card_form();
            }, 

            // Valida as informaes fornecidas
            validate_card_info() {

                // Para controle do resultado
                let output = true;

                // Validao de ttulo de carto
                if (
                    validate_attribute_by_regex (
                        this, 
                        this.card_title, 
                        alphanumeric_parser, 
                        "card_title_is_empty", 
                        "card_title_is_valid"
                    ) === false
                ){
                    output = false;
                }

                // Validao de nmero do carto
                if (
                    validate_attribute_by_regex (
                        this, 
                        this.card_number, 
                        new RegExp("^([0-9]{4} [0-9]{4} [0-9]{4} [0-9]{4})$", "g"), 
                        "card_number_is_empty", 
                        "card_number_is_valid"
                    ) === false
                ){
                    output = false;
                }

                // Validao de data de validade
                if (
                    validate_attribute_by_regex (
                        this, 
                        this.expiration_date, 
                        /* eslint-disable */
                        new RegExp("^[0-9]{4}-[0-9]{2}$", "g"), 
                        /* eslint-enable */
                        "expiration_date_is_empty", 
                        "expiration_date_is_valid"
                    ) === false
                ){
                    output = false;
                }

                // Validao de proprietrio
                if (
                    validate_attribute_by_regex (
                        this, 
                        this.cardholder, 
                        alphanumeric_parser, 
                        "cardholder_is_empty", 
                        "cardholder_is_valid"
                    ) === false
                ){
                    output = false;
                }

                // Validao de cdigo de segurana
                if (
                    validate_attribute_by_regex (
                        this, 
                        this.security_code, 
                        new RegExp("^[0-9]{3}$", "g"), 
                        "security_code_is_empty", 
                        "security_code_is_valid"
                    ) === false
                ){
                    output = false;
                }

                // Validao de senha
                if (
                    validate_attribute_by_callback (
                        this, 
                        this.password, 
                        password => {return password.length >= 3}, 
                        "password_is_empty", 
                        "password_is_valid"
                    ) === false
                ){
                    output = false;
                }

                return output;
            }, 

            // Valida a senha do usurio
            validate_password: async function() {
                let output = true;
                await validate_password_by_id(store.getters.user_id, this.password).then(res => {
                    if(res === true){
                        this.password_is_valid = true;
                    }else{
                        this.password_is_valid = false;
                        output = false;
                    }
                });
                return output;
            }, 

            // Valida as informaes e a senha do usurio
            validate_card_info_and_password: async function() {

                // Para controle da sada
                let output = false;

                // Validao das informaes e da senha
                if(this.validate_card_info() === true) {
                    await this.validate_password().then(res => {
                        if(res === true){
                            output = true;
                        }
                    });
                }

                return output;
            }, 

            // Adiciona um novo carto
            add_card: async function() {
                
                // Validao dos dados e da senha
                this.validate_card_info_and_password().then(res => {
                    if(res === true){

                        // Cria o novo carto
                        let credit_card = {
                            user: store.getters.user_id, 
                            number: this.card_number, 
                            title: this.card_title, 
                            security_code: this.security_code, 
                            cardholder: this.cardholder, 
                            expiration_date: this.expiration_date, 
                        };

                        // Adio do novo carto
                        add_credit_card(credit_card);

                        // Atualizao dos dados da pgina
                        this.cards_data.push(credit_card);
                        this.cancel_card_addition();

                        // Avisa o usurio
                        alert("Novo carto de crdito cadastrado com sucesso.");
                    }
                });
            }, 

            // Inicializa a modificao de um carto pr-existente
            start_card_update(card) {

                // Verifica se est adicionando um carto
                if(this.adding_card === true){
                    this.cancel_card_addition();
                }

                // Obteno dos dados do carto alvo
                this.previous_card_number = card.number;
                this.card_number = card.number;
                this.card_title = card.title;
                this.security_code = card.security_code;
                this.cardholder = card.cardholder;
                this.expiration_date = card.expiration_date;

                // Atualizao da pgina
                this.updating_card = true;
                window.scrollTo(0,60);
            }, 

            // Cancela a modificao de um carto pr-existente
            cancel_card_update() {
                this.updating_card = false;
                window.scrollTo(0,60);
                this.reset_card_form();
            }, 

            // Atualiza um carto pr-existente
            update_card: async function() {

                // Validao dos dados e da senha
                this.validate_card_info_and_password().then(res => {
                    if(res === true){

                        // Cria o novo carto
                        let credit_card = {
                            user: store.state.user.id, 
                            number: this.card_number, 
                            title: this.card_title, 
                            security_code: this.security_code, 
                            cardholder: this.cardholder, 
                            expiration_date: this.expiration_date, 
                        };

                        // Atualizao do carto
                        update_credit_card(this.previous_card_number, credit_card);

                        // Atualizao dos dados da pgina
                        let card_index = this.cards_data.findIndex(element => {
                            return (
                                element.user === credit_card.user &&
                                element.number === this.previous_card_number
                            );
                        });
                        if(card_index >= 0){
                            this.cards_data[card_index] = credit_card;
                        }
                        this.cancel_card_update();

                        // Avisa o usurio
                        alert("Carto de crdito atualizado com sucesso.");
                    }
                });
            }, 

            // Remove um carto de crdito pr-existente
            delete_card: async function() {

                // Validao senha
                this.validate_password().then(res => {
                    if(res === true){

                        // ID do usurio
                        const user_id = store.getters.user_id;

                        // Remoo do carto
                        delete_credit_card(user_id, this.previous_card_number);

                        // Atualizao dos dados da pgina
                        let card_index = this.cards_data.findIndex(element => {
                            return (
                                element.user === user_id &&
                                element.number === this.previous_card_number
                            );
                        });
                        if(card_index >= 0){
                            this.cards_data.splice(card_index, 1);
                        }
                        this.cancel_card_update();

                        // Avisa o usurio
                        alert("Carto de crdito removido com sucesso.");
                    }
                });
            }, 
        }, 
    }

</script>


<!-- .:::: STYLE ::::. -->
<style>
    @import "../css/profile-form.css";
</style>