<!-- .:::: TEMPLATE ::::. -->
<template>

    <!-- Seo de endereos -->
    <div v-if="!adding_address">

        <!-- Informaes de rodap -->
        <h2 class="form-h2">Endereos de Entrega</h2>
        <p class="text-common-color">Quantia de elementos cadastrados: {{addresses_data.length}}.</p><br>

        <!-- Endereos cadastrados -->
        <table class="form-table" v-if="addresses_data.length > 0">

            <!-- Cabealho da tabela -->
            <tr class="form-table-header-row">
                <th class="form-table-title-item">Ttulo do Endereo</th>
                <th class="form-table-center-item">Prvia do Endereo</th>
                <th class="form-table-last-item">Modificar Endereo</th>
            </tr>

            <!-- Itens da tabela -->
            <tr 
                v-for="(address, index) in addresses_data" 
                :key="address" 
                :class="{
                    'form-table-item-row-0' : index % 2 === 0, 
                    'form-table-item-row-1' : index % 2 !== 0, 
                    'form-table-item-row-emphasis' : active_address === index
                }">
                <td>
                    <label>{{address.title}}</label>
                </td>
                <td>
                    <label>{{get_address_preview(address)}}</label>
                </td>
                <td>
                    <a @click="select_delivery_address(address, index)">{{active_address === index ? 'Selecionado' : 'Selecionar'}}</a>
                </td>
            </tr>

        </table>

        <!-- Botes de gerenciamento de endereos -->
        <div class="form-update-buttons-section">
            <button @click="start_address_addition()" class="form-button standard-button">Adicionar endereo</button>
            <button @click="go_to_addresses_page()" class="form-button orange-button">Gerenciar endereos</button>
        </div>

    </div>

    <!-- Seo de adio e modificao de endereos -->
    <div v-if="adding_address">

        <!-- Ttulo do endereo -->
        <h2 class="form-h2">Ttulo do endereo</h2>
        <p class="text-common-color" style="margin-bottom: 1rem;">(somente para referncia)</p>
        <input 
            v-model="address_title" 
            type="text"
            class="form-info-container text"
            :class="{'form-normal-input-text': address_title_is_valid && !address_title_is_empty}">
        <p v-if="!address_title_is_valid" class="form-failed-input-text">O ttulo informado  invlido (deve conter somente caracteres alfanumricos).</p>
        <p v-if="address_title_is_empty" class="form-failed-input-text">Este campo  obrigatrio.</p>


        <!-- CEP do endereo -->
        <h2 class="form-h2">CEP</h2>
        <input 
            v-model="cep" 
            type="text" 
            placeholder="12345-123"
            class="form-info-container text"
            :class="{'form-normal-input-text': cep_is_valid && !cep_is_empty}">
        <p v-if="!cep_is_valid" class="form-failed-input-text">O CEP informado  invlido.</p>
        <p v-if="cep_is_empty" class="form-failed-input-text">Este campo  obrigatrio.</p>


        <div class="form-double-section">

            <!-- Estado do endereo -->
            <div class="form-vertical-section">
                <h2 class="form-h2">Estado</h2>
                <select 
                    v-model="address_state" 
                    class="form-info-container text"
                    :class="{'form-normal-input-text': address_state_is_valid && !address_state_is_empty}">
                    <option v-for="element in states_list" :key="element" :value="element">{{element}}</option>
                </select>
                <p v-if="!address_state_is_valid" class="form-failed-input-text">O estado informado  invlido.</p>
                <p v-if="address_state_is_empty" class="form-failed-input-text">Este campo  obrigatrio.</p>
            </div>

            <!-- Cidade do endereo -->
            <div class="form-vertical-section">
                <h2 class="form-h2">Cidade</h2>
                <input 
                    v-model="city" 
                    type="text" 
                    class="form-info-container text"
                    :class="{'form-normal-input-text': city_is_valid && !city_is_empty}">
                <p v-if="!city_is_valid" class="form-failed-input-text">A cidade informada  invlida.</p>
                <p v-if="city_is_empty" class="form-failed-input-text">Este campo  obrigatrio.</p>
            </div>

        </div>


        <div class="form-double-section">

            <!-- Bairro do endereo -->
            <div class="form-vertical-section">
                <h2 class="form-h2">Bairro</h2>
                <input 
                    v-model="district" 
                    type="text" 
                    class="form-info-container text"
                    :class="{'form-normal-input-text': district_is_valid && !district_is_empty}">
                <p v-if="!district_is_valid" class="form-failed-input-text">O bairro informado  invlido.</p>
                <p v-if="district_is_empty" class="form-failed-input-text">Este campo  obrigatrio.</p>
            </div>

            <!-- Rua do endereo -->
            <div class="form-vertical-section">
                <h2 class="form-h2">Rua</h2>
                <input 
                    v-model="street" 
                    type="text" 
                    class="form-info-container text"
                    :class="{'form-normal-input-text': street_is_valid && !street_is_empty}">
                <p v-if="!street_is_valid" class="form-failed-input-text">A rua informada  invlida.</p>
                <p v-if="street_is_empty" class="form-failed-input-text">Este campo  obrigatrio.</p>
            </div>

        </div>


        <div class="form-double-section">

            <!-- Nmero do endereo -->
            <div class="form-vertical-section">
                <h2 class="form-h2" style="margin-bottom: 3.0rem;">Nmero</h2>
                <input 
                    v-model="number" 
                    type="text" 
                    maxlength="8"
                    class="form-info-container text"
                    :class="{'form-normal-input-text': number_is_valid && !number_is_empty}">
                <p v-if="!number_is_valid" class="form-failed-input-text">O nmero informado  invlido.</p>
                <p v-if="number_is_empty" class="form-failed-input-text">Este campo  obrigatrio.</p>
            </div>

            <!-- Complemento do endereo -->
            <div class="form-vertical-section">
                <h2 class="form-h2">Complemento</h2>
                <p class="text-common-color" style="margin-bottom: 1rem;">(opcional)</p>
                <input 
                    v-model="complement" 
                    type="text" 
                    maxlength="16"
                    class="form-info-container text"
                    :class="{'form-normal-input-text': complement_is_valid}">
                <p v-if="!complement_is_valid" class="form-failed-input-text">O complemento informado  invlido.</p>
            </div>

        </div>


        <!-- Confirmao de senha -->
        <h2 class="form-h2">Confirme sua Senha</h2>
        <input 
            v-model="password" 
            placeholder=""
            type="password" 
            class="form-info-container text"
            :class="{'form-normal-input-text': password_is_valid && !password_is_empty}">
        <p v-if="!password_is_valid" class="form-failed-input-text">A senha informada  invlida.</p>
        <p v-if="password_is_empty" class="form-failed-input-text">Este campo  obrigatrio.</p>
        <div class="form-info-container text" style="margin-bottom:2rem;">
            <input class="save-address-input"
                v-model="save_address"
                value="false"
                type="checkbox">
            <span class="text">Salvar este endereo em sua conta.</span>
        </div>

        <!-- Botes de ao -->
        <div class="form-update-buttons-section">
            <button @click="add_address()" class="form-button standard-button">Selecionar endereo</button>
            <button @click="cancel_address_addition()" class="form-button gray-button">Cancelar</button>
        </div>

    </div>

</template>


<!-- .:::: SCRIPT ::::. -->
<script>

    // Para manipulao do banco de dados
    import store from '@/store';
    import { 
        add_delivery_address, 
        select_user_delivery_addresses, 
    } from '@/utils/database-management';

    // Para validao de formulrio
    import { 
        validate_attribute_by_regex, 
        validate_attribute_by_callback, 
        validate_optional_attribute_by_regex,
        validate_password_by_id  
    } from "@/utils/form-validation.js";
    import { alphanumeric_parser } from '@/utils/utils';

    // Lgica local
    export default {

        // Nome do componente
        name: "PurchaseAddress", 

        // Dados locais
        data() {
            return {

                // Lista de estados brasileiros
                states_list: [
                    "Acre", "Alagoas", "Amap", "Amazonas", 
                    "Bahia", "Cear", "Distrito Federal", "Esprito Santo", 
                    "Gois", "Maranho", "Mato Grosso", "Mato Grosso do Sul", 
                    "Minas Gerais", "Par", "Paraba", "Paran", 
                    "Pernambuco", "Piau", "Rio de Janeiro", "Rio Grande do Norte", 
                    "Rio Grande do Sul", "Rondnia", "Roraima", "Santa Catarina",
                    "So Paulo","Sergipe","Tocantins",
                ], 

                // Endereos de entrega
                addresses_data: [], 

                // Para controle de adio e modificao de endereos
                adding_address: false, 
                save_address: false, 

                // Controle de ttulo
                address_title: "", 
                address_title_is_valid: true, 
                address_title_is_empty: false, 

                // Controle de CEP
                cep: "", 
                cep_is_valid: true, 
                cep_is_empty: false, 

                // Controle de estado
                address_state: "", 
                address_state_is_valid: true, 
                address_state_is_empty: false, 

                // Controle de cidade
                city: "", 
                city_is_valid: true, 
                city_is_empty: false, 

                // Controle de bairro
                district: "", 
                district_is_valid: true, 
                district_is_empty: false, 

                // Controle de rua
                street: "", 
                street_is_valid: true, 
                street_is_empty: false, 

                // Controle de nmero de endereo
                number: "", 
                number_is_valid: true, 
                number_is_empty: false, 

                // Controle de complemento
                complement: "", 
                complement_is_valid: true, 
                
                // Controle de senha
                password: "", 
                password_is_valid: true, 
                password_is_empty: false, 
            };
        }, 

        // Aquisio dos dados do usurio
        created() {
            select_user_delivery_addresses(store.getters.user_id).then(res => {
                if(res != null) {
                    this.addresses_data = res;
                }
            });
        }, 

        // Atributos computados
        computed: {
            active_address: {
                get() {
                    return store.getters.delivery_address_index;
                }, 
                set(value) {
                    store.commit("set_delivery_address_index", value);
                },
            }, 
        }, 

        // Mtodos auxiliares
        methods: {

            // Navega para a pgina de gerenciamento de endereos de entrega
            go_to_addresses_page() {
                this.$router.push({name: 'profile-addresses'});
                window.scrollTo(0,0);
            }, 

            // Seleciona um endereo pr-existente
            select_delivery_address(address, index) {
                this.active_address = index;
                store.commit("set_delivery_address", address);
            }, 

            // Obtm a pr-visualizao de um endereo
            get_address_preview(address) {
                return address.street + ", " + address.number;
            }, 

            // Redefine os dados de formulrio
            reset_address_form() {

                // Ttulo
                this.address_title = "";
                this.address_title_is_valid = true;
                this.address_title_is_empty = false;

                // CEP
                this.cep = "";
                this.previous_cep = "";
                this.cep_is_valid = true;
                this.cep_is_empty = false;

                // Estado
                this.address_state = "";
                this.address_address_state_is_valid = true;
                this.address_address_state_is_empty = false;

                // Cidade
                this.city = "";
                this.city_is_valid = true;
                this.city_is_empty = false;

                // Bairro
                this.district = "";
                this.district_is_valid = true;
                this.district_is_empty = false; 

                // Rua
                this.street = "";
                this.street_is_valid = true;
                this.street_is_empty = false;

                // Nmero de endereo
                this.number = "";
                this.number_is_valid = true;
                this.number_is_empty = false;

                // Complemento
                this.complement = "";
                this.complement_is_valid = true;
                
                // Senha
                this.password = "";
                this.password_is_valid = true;
                this.password_is_empty = false;
            }, 
            
            // Inicializa a adio de um novo endereo de crdito
            start_address_addition() {
                this.adding_address = true;
                window.scrollTo(0,60);
            }, 

            // Cancela a adio de um novo endereo de crdito
            cancel_address_addition() {
                this.adding_address = false;
                window.scrollTo(0,60);
                this.reset_address_form();
            }, 

            // Valida as informaes fornecidas
            validate_address_info() {

                // Para controle do resultado
                let output = true;

                // Validao de ttulo
                if (
                    validate_attribute_by_regex (
                        this, 
                        this.address_title, 
                        alphanumeric_parser, 
                        "address_title_is_empty", 
                        "address_title_is_valid"
                    ) === false
                ){
                    output = false;
                }

                // Validao de CEP
                if (
                    validate_attribute_by_regex (
                        this, 
                        this.cep, 
                        new RegExp("^[0-9]{5}-[0-9]{3}$", "g"), 
                        "cep_is_empty", 
                        "cep_is_valid"
                    ) === false
                ){
                    output = false;
                }

                // Validao de estado
                if (
                    validate_attribute_by_regex (
                        this, 
                        this.address_state, 
                        alphanumeric_parser, 
                        "address_state_is_empty", 
                        "address_state_is_valid"
                    ) === false
                ){
                    output = false;
                }

                // Validao de cidade
                if (
                    validate_attribute_by_regex (
                        this, 
                        this.city, 
                        alphanumeric_parser, 
                        "city_is_empty", 
                        "city_is_valid"
                    ) === false
                ){
                    output = false;
                }

                // Validao de bairro
                if (
                    validate_attribute_by_regex (
                        this, 
                        this.district, 
                        alphanumeric_parser, 
                        "district_is_empty", 
                        "district_is_valid"
                    ) === false
                ){
                    output = false;
                }

                // Validao de rua
                if (
                    validate_attribute_by_regex (
                        this, 
                        this.street, 
                        alphanumeric_parser, 
                        "street_is_empty", 
                        "street_is_valid"
                    ) === false
                ){
                    output = false;
                }

                // Validao de nmero
                if (
                    validate_attribute_by_regex (
                        this, 
                        this.number, 
                        new RegExp("^[0-9]+$", "g"), 
                        "number_is_empty", 
                        "number_is_valid"
                    ) === false
                ){
                    output = false;
                }

                // Validao de complemento (opcional)
                if (
                    validate_optional_attribute_by_regex (
                        this, 
                        this.complement, 
                        alphanumeric_parser, 
                        "complement_is_valid"
                    ) === false
                ){
                    output = false;
                }

                // Validao de senha
                if (
                    validate_attribute_by_callback (
                        this, 
                        this.password, 
                        password => {return password.length >= 3}, 
                        "password_is_empty", 
                        "password_is_valid"
                    ) === false
                ){
                    output = false;
                }

                return output;
            }, 


            // Valida as informaes e a senha do usurio
            validate_address_info_and_password: async function() {

                // Para controle da sada
                let output = true;

                // Validao das informaes e da senha
                if(this.validate_address_info() === true) {
                    try{
                        await validate_password_by_id(store.getters.user_id, this.password).then(res => {
                            if(res === true){
                                this.password_is_valid = true;
                            }else{
                                this.password_is_valid = false;
                                output = false;
                            }
                        });
                    }catch(e){
                        alert(e);
                        output = false;
                    }
                }else{
                    output = false;
                }

                return output;
            }, 


            // Adiciona um novo endereo
            add_address: async function() {
                
                // Validao dos dados e da senha
                this.validate_address_info_and_password().then(res => {
                    if(res === true){

                        // Cria o novo endereo de entrega
                        const delivery_address = {
                            user: store.getters.user_id, 
                            zip: this.cep, 
                            title: this.address_title, 
                            state: this.address_state, 
                            city: this.city, 
                            district: this.district, 
                            street: this.street, 
                            number: this.number, 
                            complement: this.complement, 
                        };
                        store.commit("set_delivery_address", delivery_address);

                        // Caso queira salvar em base de dados
                        if(this.save_address === true) {

                            // Adio do novo endereo
                            add_delivery_address(delivery_address);

                            // Atualizao dos dados da pgina
                            this.active_address = this.addresses_data.length;
                            this.addresses_data.push(delivery_address);

                            // Avisa ao usurio
                            alert("Novo endereo de entrega cadastrado com sucesso.");
                        }else{
                            alert("Endereo de entrega selecionado com sucesso.");
                        }
                        
                        // Finalizao
                        this.cancel_address_addition();
                    }
                });
            }, 
        }, 
    }

</script>


<!-- .:::: STYLE ::::. -->
<style scoped>
    @import "../css/profile-form.css";
    @import "../css/colors.css";
    .save-address-input {
        border: var(--box-border);
        box-shadow: 0.05rem 0.05rem 0.2rem rgba(0, 0, 0, 0.281);
        margin-right: 1rem;
    }
</style>